<template>
  <div class="hvac">
    <v-container>
      <v-row justify-center dense>
        <v-col cols="12" sm="6" md="6" lg="4" xl="3"><HVAC :cursor="'pointer'"/></v-col>
      </v-row>
    </v-container>
  <ReadingsDetails v-if="dialog" v-model="dialog" :id="activeID"/>
  </div>
</template>

<script>
import HVAC from '@/components/HVAC.vue'
import ReadingsDetails from '@/components/ReadingsDetailDialog.vue'

import eventBus from "../eventBus"

export default {
    name: 'HVACView',
    components: {
        HVAC,
        ReadingsDetails
    },
    data: () => ({
        dialog:false,
        activeID:'',
    }),
    created(){
        eventBus.$on('details',this.openDetailsDialog)
    },
    mounted (){
        window.scrollTo(0, 0)
    },
    methods:{
        openDetailsDialog(id){
        this.activeID = id
        this.dialog = !this.dialog 
       // console.log("openDetailsDialog",id)
        },
    }
}
</script>

<style scoped>
 .container {
    width: 100%;
    padding: 0px; 
  } 
  .hvac {
    overflow: hidden;
  }
</style>