<template>
    <div id="app">
            <v-dialog v-model="show" max-width="700px" scrollable @click:outside="revealTag = false" :fullscreen ="$vuetify.breakpoint.mobile">
                <v-card elevation="24" outlined >
                    <!-- <v-card-title >{{dialogObj.id}} <v-spacer></v-spacer><v-subheader class="font-italic">{{dialogObj.type}}</v-subheader></v-card-title> -->
                    <v-card-title class="headline px-3">{{id}}<v-spacer></v-spacer><v-btn icon @click="show=false"><v-icon>{{$icon.mdiClose}}</v-icon></v-btn></v-card-title>
                    <v-subheader class="font-italic mt-n6">{{registerType()}}</v-subheader>
                    <v-card-text class="pa-0"><pre  :class="['', { 'fs-13' : $vuetify.breakpoint.mobile }]">{{$root.readingsTest[id]}}</pre></v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions class="py-4">
                        <v-btn class="ml-2 mr-3" color="primary" @click="eventDialog = true"><v-icon left>{{$icon.mdiAccessPointPlus}}</v-icon>Event</v-btn>
                        <div class="text-center">
                            <v-menu offset-y top >
                              <template v-slot:activator="{ on, attrs }">
                                  <v-btn color="primary" v-bind="attrs" v-on="on"><v-icon left>{{$icon.mdiPencil}}</v-icon>Edit</v-btn>
                              </template>
                                <v-list>
                                  <v-list-item v-for="(item, index) in editMenu" :key="index">
                                    <v-btn @click="edit(item.title)" :disabled="item.disabled">{{ item.title }}</v-btn>
                                  </v-list-item>
                                </v-list>
                            </v-menu>
                        </div>
                        <v-spacer></v-spacer>
                    </v-card-actions>
                    <v-expand-transition>
                        <v-card outlined v-if="revealTag" class="transition-fast-in-fast-out v-card--reveal d-flex flex-column" style="height: 100%;">
                            <v-card-title class="headline px-3"><v-icon class="pr-3">{{$icon.mdiCodeBrackets}}</v-icon>{{id}} - Edit Tags  <v-spacer></v-spacer></v-card-title>
                            <v-subheader class="font-italic mt-n7">array</v-subheader>
                            <v-card-text class="pb-0" scrollable>
                                <!-- <v-text-field outlined solo @click:clear="removeTag(k)" clearable v-for="(tag,k) in tagArray" :key='k' v-model="tagArray[k]" :rules="rules.name"></v-text-field> -->
                                <v-combobox outlined small-chips multiple label="Event Tags" :items="tagAll" v-model="tagArray" ></v-combobox>
                            </v-card-text>
                            <v-spacer></v-spacer>
                            <v-divider></v-divider>
                            <v-card-actions class="">
                                <v-spacer></v-spacer>
                                <v-btn
                                    color="primary"
                                    text
                                    @click="revealTag = false"
                                >
                                    cancel
                                </v-btn>
                                <v-btn
                                    color="primary"
                                    text
                                    @click="revealTag = false & addTags()"
                                >
                                    OK
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-expand-transition>
                    <v-expand-transition >
                      <v-card outlined v-if="revealViewRule" class="transition-fast-in-fast-out v-card--reveal d-flex flex-column" style="height: 100%;">
                            <v-card-title class="headline px-3" ><v-icon class="pr-3">{{$icon.mdiCodeJson}}</v-icon>{{id}} - View Rules  <v-spacer></v-spacer></v-card-title>
                            <v-subheader class="font-italic mt-n7">object</v-subheader>
                            <v-card-text class="pb-0" scrollable style="overflow: auto;">
                              <!-- <v-card-subtitle class=" text-h6"> Value </v-card-subtitle>
                              <v-data-table >


                              </v-data-table>
                                -->
                                <v-jsoneditor v-model="viewRuleObject" :options="options"  :plus="false" height='60vh' class="mb-2 "/>
                                <div class="d-flex justify-center">
                                  <v-btn class="mr-2" color="primary" @click="addRule('value')">+ Value Rule</v-btn><v-btn color="primary" @click="addRule('box')">+ Box Rule</v-btn>
                                  </div>
                                  <v-card-text class="d-flex justify-center"><span>This allows you to modify the view based on a "value". These rules are global. It will apply to everyones view. 
                                    If making changes from the desktop, make sure the changes are acceptable for mobile and vice versa. A basic knowlage of JSON and CSS is required.</span>
                                  </v-card-text>
                                
                            </v-card-text>
                            <v-spacer></v-spacer>
                            <v-divider></v-divider>
                            <v-card-actions class="">
                                <v-spacer></v-spacer>
                                <v-btn
                                    color="primary"
                                    text
                                    @click="revealViewRule = false"
                                >
                                    cancel
                                </v-btn>
                                <v-btn
                                  :disabled="ok"
                                    color="primary"
                                    text
                                    @click="revealViewRule = false & updateViewRule()"
                                >
                                    OK
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-expand-transition>
                </v-card>
            </v-dialog>
           <!-- <EventsEditDialog v-model="test" v-bind:dialogObj="dialogObj" />  -->
           <EventsEditDialog v-model="eventDialog" v-if="eventDialog"  :mode='eventMode' :events="emptyEventWithID" />
      <!--   </v-app> -->
    </div>
</template>

<script>

import EventsEditDialog from '@/components/EventsEditDialog.vue'

//import VJsoneditor from 'v-jsoneditor/src/index'
const VJsoneditor = () => import('v-jsoneditor/src/index')

export default {
  name:'readingsDetailsDialog',
  components: {
    EventsEditDialog,
    VJsoneditor
  },
  props: ["value","id"],
  data: (x) => ({
      options:{
        search:false, 
        mode:'code', 
         
        mainMenuBar:false, 
        onValidationError: function (errors){
          console.log(errors)
          x.jsonEditorErrors = errors
          if(x.jsonEditorErrors. length > 0){
            x.ok = true
          } else {
            x.ok = false
          }
          },
         schema:x.valueRule,
         onValidate: function(json){
           let allowedKeys = ['readingsValue','comparison','value','css']
           var errors = []
           /* if ( json.value && !json.box) { */
          if(!json.value){
            errors.push({
              path: ['value'],
              message: 'Required property "value" missing.'
            });
          } else {
            if(!Array.isArray(json.value)){
              errors.push({
                path: ['value'],
                message: 'Value must be an array.'
              });
            } else {
              
              json.value.forEach(x => {
                
                if(typeof x !== 'object'){
                  errors.push({
                    path: ['value'],
                    message: 'Value array must contain objects.'
                  });
                } 
                let valueKeyCount = 0
                Object.keys(x).forEach(key => {
                  valueKeyCount ++
                  if(!allowedKeys.includes(key)){
                    errors.push({
                      path: ['value'],
                      message: 'Value object must contain readingsValue, comparison, value, and css key properties'
                    });
                  } else {
                    if(key === 'readingsValue'){
                      if(!x[key].match(/^(value|mathValue|rawValue)$/)){
                        errors.push({
                          path: ['value'],
                          message: 'Required - readingsValue: value|mathValue|rawValue'
                      });
                      }
                    }
                    if(key === 'comparison'){
                      if(!x[key].match(/^(equal|notEqual|lessThan|lessThanOrEqual|graterThan|graterThanOrEqual|between|notBetween)$/)){
                        errors.push({
                          path: ['value'],
                          message: 'Required - comparison: equal|notEqual|lessThan|lessThanOrEqual|graterThan|graterThanOrEqual|between|notBetween'
                      });
                      }
                    }
                    if(key === 'css'){
                      
                      if(typeof x[key] !== 'object'){
                        errors.push({
                          path: ['value'],
                          message: 'css must be an object'
                      });
                      } 
                    }
                  }
                  
                })
                if( valueKeyCount < 4 ){
                  errors.push({
                          path: ['value'],
                          message: 'value object must contain readingsValue, comparison, value, and css key properties'
                      });
                }
              });
            }
          }
          if(!json.box){
            errors.push({
              path: ['box'],
              message: 'Required property "box" missing.'
            });
          } else {
            if(!Array.isArray(json.box)){
              errors.push({
                path: ['box'],
                message: 'Box must be an array.'
              });
            } else {  
              json.box.forEach(x => {
                if(typeof x !== 'object'){
                  errors.push({
                    path: ['box'],
                    message: 'box array must contain objects.'
                  });
                } 
                let keyCount = 0
                Object.keys(x).forEach(key => {
                  keyCount ++
                  if(!allowedKeys.includes(key)){
                    errors.push({
                      path: ['box'],
                      message: 'Box object must contain readingsValue, comparison, value, and css key properties'
                    });
                  } else {
                    if(key === 'readingsValue'){
                      if(!x[key].match(/^(value|mathValue|rawValue)$/)){
                        errors.push({
                          path: ['box'],
                          message: 'Required - readingsValue: value|mathValue|rawValue'
                      });
                      }
                    }
                    if(key === 'comparison'){
                      if(!x[key].match(/^(equal|notEqual|lessThan|lessThanOrEqual|graterThan|graterThanOrEqual|between|notBetween)$/)){
                        errors.push({
                          path: ['box'],
                          message: 'Required - comparison: equal|notEqual|lessThan|lessThanOrEqual|graterThan|graterThanOrEqual|between|notBetween'
                      });
                      }
                    }
                    if(key === 'css'){
                      
                      if(typeof x[key] !== 'object'){
                        errors.push({
                          path: ['box'],
                          message: 'css must be an object'
                      });
                      } 
                    }
                  }
                  
                })
                if( keyCount < 4 ){
                  errors.push({
                          path: ['box'],
                          message: 'box object must contain readingsValue, comparison, value, and css key properties'
                      });
                }
              });
            //}
            }
          }

    return errors;
         }
      },
      eventMode:'create',
      eventDialog:false,
      test:false,
      revealTag:false,
      revealViewRule:false,
      tagAll:[],
      tagArray:[],
      rules:{name: [val => (val || '').length > 0 || 'No empty fields'],},
      status:{},
      editMenu:[
        {title:'Reset Min',disabled:true},
        {title:'Reset Max',disabled:true},
        {title:'Edit Tag',disabled:false},
        {title:'View Rule',disabled:false}
      ],
      viewRuleObject:{},
      jsonEditorErrors:[],
      ok:true,
      
           


 
      


  }),
    computed: {
    show: {
      get () {
        return this.value
      },
      set (value) {
         this.$emit('input', value)
      }
    },
    
    
    
    
  },
  created(){
    
    //console.log(this.dialogObj)
    //this.viewRuleObject = this.$readings[this.id].view.rule
    //this.viewRuleObject = this.$root.readingsTest[this.id].view.rule
    //console.log(this.$readings[this.id].view.rule)
    //console.log(this.id)
    //let vm = this
    this.tagNames()
    
    this.emptyEventWithID = this.$clone(this.$emptyEvent)
    //this.emptyEventWithID.register_id = this.dialogObj.id
    this.emptyEventWithID.register_id = this.id
    //this.emptyEventWithID.alias = this.$readings[this.dialogObj.id].view.alias
    this.emptyEventWithID.alias = this.$root.readingsTest[this.id].view.alias
    //console.log("value =>",this.value)

    /* let n = this.dialogObj.id.includes('_')
    if(n){
      this.dialogObj.type = 'binary'
      this.editMenu[0].disabled = true
      this.editMenu[1].disabled = true
    } else {
      this.dialogObj.type = 'word'
      this.editMenu[0].disabled = false
      this.editMenu[1].disabled = false
      this.editMenu[2].disabled = false
    } */
    //console.log("look here ==>",this.$root.readingsTest[this.id])
    /* let n = this.id.includes('_')
    if(n){
      this.registerType = 'binary'
      //this.editMenu[0].disabled = true
      //this.editMenu[1].disabled = true
    } else {
      this.registerType.type = 'word'
      //this.editMenu[0].disabled = false
      //this.editMenu[1].disabled = false
      //this.editMenu[2].disabled = false
    } */
      
  },
  methods:{
    addRule(type){
      let emptyRule = {
        "readingsValue":"",
        "comparison":"",
        "value":"",
        "css":{}
      }

      if(type === 'value'){
        this.viewRuleObject.value.push(emptyRule)
      } else if(type === 'box'){
        this.viewRuleObject.box.push(emptyRule)
      }
      //console.log(this.viewRuleObject)

    },
    registerType(){
      let n = this.id.includes('_')
      if(n){
        this.editMenu[0].disabled = true
        this.editMenu[1].disabled = true
        return 'binary'
        
      } else {
        this.editMenu[0].disabled = false
        this.editMenu[1].disabled = false
        this.editMenu[2].disabled = false
        return 'word'
        
      }
    },
    testing(e){
      console.log(e)
    },
    updateViewRule(){
      let postData = {}
      postData._id = this.id
      postData.rule = this.viewRuleObject
/*       this.$http.post('/api/modbus/updateViewRule',postData,function(err,ret){
        console.log(err || ret)
      }) */
      this.$http.post('/api/modbus/updateViewRule',postData).then( res => {
        console.log(res)
      })
    },
    onError(e){
      console.log(e)
    },
      edit(item){
        //let vm = this
        switch (item) {
          case "Reset Min":
            //this.$http.get("/api/modbus/resetMin/"+this.id,function(err,ret){console.log(ret.data)})
            this.$http.get(`/api/modbus/resetMin/${this.id}`).then(res => {console.log(res)})
            break;
          case "Reset Max":
              //this.$http.get("/api/modbus/resetMax/"+this.id,function(err,ret){console.log(ret.data)})
              this.$http.get(`/api/modbus/resetMax/${this.id}`).then(res => {console.log(res)})
              break;
          case "Edit Tag":
              this.revealTag = !this.revealTag
              /* this.$http.get('/api/modbus/getTagByID/'+this.id,function(err,ret){
                vm.tagArray = ret.data.results
              }) */
              this.$http.get(`/api/modbus/getTagByID/${this.id}`).then(res => {this.tagArray = res.results}) 
              break;
          case "View Rule":
            this.viewRuleObject = this.$root.readingsTest[this.id].view.rule
            this.revealViewRule = !this.revealViewRule
            break;
          default:
            break;
        }
      },
    addTagItem(){
      this.tagArray.push("")
    },
    addTags(){
      //let vm = this
      var postData = {
        _id: this.id,
        tag: this.tagArray
      }; 
     /*  this.$http.post('/api/modbus/updateTags',postData,function(err,ret){
        //console.log(err || ret)
        vm.status = ret
        vm.tagNames()
      }) */
      this.$http.post('/api/modbus/updateTags',postData).then(res => {
        this.status = res
        this.tagNames()
      })
      


    },
    removeTag(index){
      this.$delete(this.tagArray,index)
    },
    tagNames(){
    //let vm = this
    /* this.$http.get("/api/readings/tagNames/",function(err,ret){
      ret.data.results.forEach(x => {
         if(!vm.tagAll.includes(x)) vm.tagAll.push(x)
      });
      vm.tagAll.sort()
      //console.log(vm.tagAll)
    }) */
    this.$http.get('/api/readings/tagNames').then(res => {
      res.results.forEach(x => {
         if(!this.tagAll.includes(x)) this.tagAll.push(x)
      });
      this.tagAll.sort()
    })
  }
  },
  
}
</script>

<style scoped>
.v-card--reveal {
  bottom: 0;
  opacity: 1 !important;
  position: absolute;
  width: 100%;
}
.fs-13{
  font-size: 12px;
  
}
pre {
  background-color: black;
  height: 100%;
  overflow: auto;
}


</style>