<template>
    <div class="px-1" :style="viewRule_Box" @click='showDialog(view._id)'>
       <div v-if="hasName" :class="boxName"  class="text-center  text--secondary">
            <span>{{view.name}}</span>
        </div>
        <div :class="[boxValue,fontStyle]" class="boxvalue text-center" > 
            <span  :style="viewRule_Value">{{view.value}}</span>
        </div> 
    </div>
</template>
<script>
import eventBus from "../eventBus";
export default {

   // props:['view','noName'],
    props:{
        view:{
            type:Object
        },
        hasName:{
            type:Boolean,
            default:true
        },
        fontSize:{
            type:String,
            default: '18px'
        },
        breakpointLG:{
            type:Boolean,
            default: false
        },
    },
    data: () => ({
       
    }),
    created(){
    },
    methods:{
        showDialog(id){
        console.log("here",id)
        eventBus.$emit('details',id)
      }
    },
    computed:{
        boxValue(){
            return{
                '--size': this.fontSize
            }
        },
        boxName(){
            if(this.$vuetify.breakpoint.name === 'lg'){
                return ['caption',this.view.style]
            } else {
                return ['subtitle-2',this.view.style]
            }
        },
        fontStyle(){
            if(this.breakpointLG){
                if(this.$vuetify.breakpoint.name === 'lg'){
                    return 'caption'
                } else {
                    return 'subtitle-2'
                }
            } else {
                return ''
            }
        }
    }
    
}
</script>
<style scoped>
    .aphase-under {
    border-bottom: 4px solid #000;
    }
    .bphase-under {
        border-bottom: 4px solid rgba(255, 59, 59, .7);
    }
    .cphase-under {
        border-bottom: 4px solid rgba(51, 102, 255, .7);
    }
    .freq-under {
        border-bottom: 4px solid rgba(11, 146, 61, .7);
    }
    .aphase-under-light {
        border-bottom: 4px solid #000;
    }
    .bphase-under-light {
        border-bottom: 4px solid #ff3b3b;
    }
    .cphase-under-light {
        border-bottom: 4px solid #36f;
    }
    .freq-under-light {
        border-bottom: 4px solid #0b923d;
    }
    .boxvalue {
        font-size: var(--size);
        font-weight: 700;
    }
    .boxtitle{
        width: 100%;
        margin-bottom: 5px;
        padding-top: 5px;
        font-size: 15px;
    }
</style>