<template>
    <v-card class="pb-3 " height="288px">
        <v-card-title class="text-center pt-1">HVAC SYSTEM</v-card-title>
        <v-card elevation="4" outlined class="mx-2 mt-n2" v-if="hasHVAC">
            <v-container class="my-n3">
                <v-layout row justify-space-around >
                    <div class="box" :style="hover" v-for="(viewItem, key) in $displayOrder($view.hvac)" :key="key">
                        <v-flex class=" pb-1" :class="$vuetify.breakpoint.lg ? ['py-0','pt-2'] : 'pa-2'" >
                            <BoxValue :view="viewItem"/>
                        </v-flex>
                    </div>
                </v-layout>
            </v-container>
        </v-card>
        <v-card elevation="4" outlined class="mx-2 mt-2 px-1" v-if="hasHVAC">
        <v-simple-table dense >
            <template v-slot:default>
                <thead>
                    <tr>
                    <th class="text-center  text--secondary  px-1" :class="$vuetify.breakpoint.lg ? 'caption' : 'subtitle-2'">
                        System
                    </th>
                    <th class="text-center  text--secondary px-1" :class="$vuetify.breakpoint.lg ? 'caption' : 'subtitle-2'">
                        Low
                    </th>
                    <th class="text-center  text--secondary px-1" :class="$vuetify.breakpoint.lg ? 'caption' : 'subtitle-2'">
                        High
                    </th>
                    <th class="text-center  text--secondary px-1" :class="$vuetify.breakpoint.lg ? 'caption' : 'subtitle-2'">
                        Suction
                    </th>
                    <th class="text-center  text--secondary px-1" :class="$vuetify.breakpoint.lg ? 'caption' : 'subtitle-2'">
                        Comp.
                    </th>
                    <!-- <th class="text-center  text--secondary px-1" :class="$vuetify.breakpoint.lg ? 'caption' : 'subtitle-2'">
                        Liq. Line
                    </th> -->
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in hvac" :key="item.name">
                        <td class="text-center pa-0 text--secondary" :class="$vuetify.breakpoint.lg ? 'caption' : 'subtitle-2'">{{ item.system }}</td>
                        <!--<td class="text-center pa-0" :class="$vuetify.breakpoint.lg ? 'caption' : 'subtitle-2'">{{ item.low }}</td>
                        <td class="text-center pa-0" :class="$vuetify.breakpoint.lg ? 'caption' : 'subtitle-2'">{{ item.high }}</td>
                        <td class="text-center pa-0" :class="$vuetify.breakpoint.lg ? 'caption' : 'subtitle-2'">{{ item.suction }}</td>
                        <td class="text-center pa-0" :class="$vuetify.breakpoint.lg ? 'caption' : 'subtitle-2'">{{ item.comp }}</td> -->
                        <!-- <td class="text-center pa-0" :class="$vuetify.breakpoint.lg ? 'caption' : 'subtitle-2'">{{ item.liquidLine }}</td> -->
                        <td class="text-center pa-0 " v-for="(viewItem, key) in $displayOrder($view[item.group])" :key="key" ><span :style="hover"><BoxValue :breakpointLG='true' :fontSize="'14px'" :hasName="false" :view="viewItem"/></span></td>
                    </tr>
                </tbody>
            </template>
        </v-simple-table>
        </v-card>
    </v-card>
</template>

<script>
    import BoxValue from './BoxValue'
    import eventBus from "../eventBus";
  export default {
      name:'HVAC',
      props:['cursor'],
      components:{
          BoxValue
      },
    data () {
      return {
          hasHVAC:true,
        hvac: [
          {
            system: '1',
            group:'hvacSystem1'
          },
          {
            system: '2',
            group:'hvacSystem2'
          },
          {
            system: '3',
            group:'hvacSystem3'
          },
          {
            system: '4',
            group:'hvacSystem4'
          },
        ],
      }
    },
    methods:{
        showDialog(id){
                //console.log("here",id)
               eventBus.$emit('details',id)
               // window.open("", "", "width=200,height=100")
            },
    }, 
    computed: {
        hover(){
            return {
                '--cursor': this.cursor
            }
        }
    },
    created(){
        if(this.$view.hvac){
            this.hasHVAC = true
        } else {
            this.hasHVAC = false
        }
    }

  }
</script>

<style scoped>

.boxtitle{
  width: 100%;
  margin-bottom: 5px;
  padding-top: 5px;
  font-size: 12px;
}
.boxvalue {
  font-size: 18px;
  font-weight: 700;
}
.box {
    cursor: var(--cursor);
    }
</style>